.dropdown-menu.lang-dropdown {
  min-width: auto;
  width: auto;
}

.dropdown-item {
  transition: 0.1s all ease-in-out;
}

.dropdown-item:active {
  background-color: #e60000;
}



/* --- EFFETTO GLOW ROSSO --- */
.nav-link.btn {
  color: #fff;
  transition: all 0.2s ease-in-out;
  text-shadow: none;
}

/* Effetto hover */
.nav-link.btn:hover {
  color: #fff;
  text-shadow: 0 0 10px #ff3333, 0 0 20px #ff3333, 0 0 30px #ff0000;
}

/* Effetto "active" — stesso effetto dell'hover */
.nav-link.btn.active,
.nav-link.btn.router-link-active {
  color: #fff !important;
  text-shadow: 0 0 10px #ff3333, 0 0 20px #ff3333, 0 0 30px #ff0000 !important;
}
